import { PageHeader } from "@/components/page-header";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { pricingPlans } from "@/lib/data";
import { cn } from "@/lib/utils";
import { CheckCircle2 } from "lucide-react";

export default function PricingPage() {
  return (
    <div className="flex-1 space-y-4 p-4 md:p-8 pt-6">
      <PageHeader
        title="Subscription Plans"
        description="Choose a plan that fits your needs and unlock more benefits."
      />
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8 max-w-6xl mx-auto pt-8">
        {pricingPlans.map((plan) => (
          <Card key={plan.id} className={cn("flex flex-col", plan.id === 'basic' && "border-primary shadow-lg")}>
            <CardHeader>
              <div className="flex items-center justify-between">
                <CardTitle className="font-headline">{plan.name}</CardTitle>
                {plan.id !== 'free' && (
                  <CheckCircle2 className={cn(
                    plan.id === 'basic' && 'text-status-orange',
                    plan.id === 'premium' && 'text-status-green',
                  )} />
                )}
              </div>
              <CardDescription>{plan.description}</CardDescription>
            </CardHeader>
            <CardContent className="flex-grow">
              <div className="mb-6">
                <span className="text-4xl font-bold font-headline">${plan.price}</span>
                <span className="text-muted-foreground">/month</span>
              </div>
              <ul className="space-y-3">
                {plan.features.map((feature, index) => (
                  <li key={index} className="flex items-start">
                    <CheckCircle2 className="h-5 w-5 text-green-500 mr-2 mt-0.5 shrink-0" />
                    <span>{feature}</span>
                  </li>
                ))}
              </ul>
            </CardContent>
            <CardFooter>
              <Button className={cn("w-full", plan.id !== 'basic' && "variant-outline")}>
                {plan.id === 'free' ? "Current Plan" : "Subscribe"}
              </Button>
            </CardFooter>
          </Card>
        ))}
      </div>
    </div>
  )
}
